.container {
   display: flex;
   flex-direction: column;
   width: 280px;
   margin: 0 auto;
}

.googleLink {
   display: flex;
   justify-content: center;
   align-items: center;
   font-family: var(--accent-font);
   font-weight: 700;
   width: 150px;
   font-size: 16px;
   line-height: 2.37;
   text-decoration: none;
   margin: 0 auto 25px;
   color: #707375;
   background-color: #F5F7FA;
   box-shadow: 0px 2px 2px rgb(9 30 63 / 15%);
}

.form {
   position: relative;
   display: flex;
   flex-direction: column;
   margin-bottom: 15px;
}

.label {
   margin-bottom: 8px;
   font-weight: 600;
   font-size: 14px;
   line-height: 1.21;
   color: var(--main-color);
}

.label::after {
   content: ' *';
   color: #F25137;
}

.input {
   margin-bottom: 20px;
   padding: 14px 8px;
   outline: none;
   border: none;
   background: #F5F7FA;
   box-shadow: inset 0px 1px 2px rgba(29, 29, 27, 0.15);
}

.input::placeholder {
   font-weight: 400;
   font-size: 14px;
   line-height: 1.21px;
   color: var(--icon-color);
}

.errName,
.errEmail,
.errPassword,
.errConfirmPassword {
   position: absolute;
   text-align: center;
   width: 100%;
   left: 0;
   top: 17%;
}

.errEmail {
   top: 39%;
}

.errPassword {
   top: 60%;
}

.errConfirmPassword {
   top: 81%;
}

.errText {
   font-size: 12px;
   color: #F25137;
}

.btn {
   box-sizing: border-box;
   border: none;
   padding: 20px 113px;
   font-weight: 600;
   font-size: 16px;
   line-height: 1.25;
   color: var(--main-color);
   background-color: var(--accent-color);
   transition: background-color 250ms ease-in-out;
}

.btn:hover {
   cursor: pointer;
   background-color: #D15807;
}

.text {
   font-weight: 500;
   font-size: 13px;
   line-height: 1.23;
   text-align: center;
   color: var(--secondary-text-color);
}

.signupLink {
   text-decoration: none;
   color: var(--accent-color);
   transition: color 250ms ease-in-out;
}

.signupLink:hover {
   cursor: pointer;
   color: #D15807;
}

/** min-width 768px breakpoint */
@media screen and (min-width: 768px) {
   .container {
      width: 400px;
      margin: 0 auto;
      padding: 40px;
      background-color: #FFFFFF;
   }

   .googleLink {
      margin: 0 auto 30px;
   }

   .form {
      margin-bottom: 20px;
   }

   .label {
      color: var(--secondary-text-color);
   }
}